.Select {
  border: 1px solid #ccc;
  padding: 10px;
  @media (max-width: 480px) {
    max-width: 280px;
  }
  option {
    font-size: 16px;
    line-height: 16px;
    color: #9ea6b2;
    &:selected {
      color: #9ea6b2;
    }
    @media (max-width: 480px) {
      width: 280px;
    }
  }
}
