.Button {
  color: #fff;
  font-family: 'Roboto', sans-serif;
  border-radius: 3px;
  transition: 0.1s ease-in;
  &_cart_green {
    font-size: 16px;
    line-height: 27px;
    padding: 10px 54px;
    text-transform: uppercase;
    background-color: #51ad33;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover {
      background-color: #368e1a;
    }
    @media (max-width: 768px) {
      padding: 10px 40px;
    }
    @media (max-width: 540px) {
      font-size: 14px;
      line-height: 22px;
      padding: 5px 20px;
    }
    @media (max-width: 540px) {
      font-size: 14px;
      line-height: 22px;
      padding: 5px 20px;
      margin-right: 15px;
    }
  }
  &_cart_grey {
    font-size: 16px;
    line-height: 27px;
    padding: 10px 26px;
    text-transform: uppercase;
    background-color: #98a1af;
    &:hover {
      background-color: #677283;
    }
    @media (max-width: 540px) {
      font-size: 12px;
      line-height: 22px;
      padding: 5px 10px;
    }
  }
  &_ordering_confirm {
    font-size: 16px;
    line-height: 27px;
    padding: 10px 96px;
    text-transform: uppercase;
    background-color: #51ad33;
    &:hover {
      background-color: #368e1a;
    }
    @media (max-width: 480px) {
      padding: 10px 50px;
    }
  }
  &_btn-small {
    font-size: 14px;
    line-height: 22px;
    padding: 7px 10px;
    // text-transform: uppercase;
    background-color: #329e01;
    &:hover {
      background-color: #267700;
    }
  }
  &_admin-primary {
    font-size: 14px;
    line-height: 14px;
    padding: 7px 25px;
    text-transform: uppercase;
    background-color: #51ad33;
    &:hover {
      background-color: #368e1a;
    }
  }
  &_loginForm {
    font-size: 16px;
    line-height: 14px;
    padding: 16px 60px;
    text-transform: uppercase;
    background-color: #51ad33;
    &:hover {
      background-color: #368e1a;
    }
  }
  &_registerForm {
    font-size: 16px;
    line-height: 14px;
    padding: 16px 16px;
    text-transform: uppercase;
    background-color: #51ad33;
    &:hover {
      background-color: #d3d3d3;
    }
  }
  &_admin-edit {
    font-size: 14px;
    line-height: 14px;
    padding: 7px 15px;
    text-transform: uppercase;
    background-color: #525252;
    &:hover {
      background-color: #333333;
    }
  }
  &_admin-delete {
    font-size: 14px;
    line-height: 14px;
    padding: 7px 15px;
    text-transform: uppercase;
    background-color: #ff0000;
    margin-left: 10px;
    &:hover {
      background-color: #c00000;
    }
  }
  &_admin-cancel {
    font-size: 14px;
    line-height: 14px;
    padding: 7px 15px;
    text-transform: uppercase;
    background-color: #ff0000;
    margin-left: 10px;
    &:hover {
      background-color: #c00000;
    }
  }
  &_filter-price-confirm {
    font-size: 14px;
    line-height: 14px;
    padding: 7px 15px;
    text-transform: uppercase;
    background-color: #51ad33;
    margin-left: 10px;
    &:hover {
      background-color: #368e1a;
    }
  }
}
