.CartItem{position:relative;display:grid;grid-template-columns:1fr 2fr 1fr 1fr 1fr 40px;text-align:center;grid-template-rows:repeat(1, 65px);color:#000;border-bottom:1px solid #000}.CartItem p{margin-top:20px}.orderDetailsContainer{max-height:0px;background:#ffe8c3;opacity:0;visibility:hidden;transition:all .4s ease 0s}.orderDetailsContainer_active{max-height:610px;opacity:1;visibility:visible}.orderDetailsHeader{background:#cecece;display:grid;grid-template-columns:1fr 2fr 1fr 1fr 1fr;align-items:center;text-align:center;padding:5px}.orderDetailsItemWrapper{background:#f0f0f0;border-bottom:1px solid #000}.orderDetailsItemWrapper div:nth-of-type(odd){background:#f8f8f8}.orderDetailsItem{display:grid;grid-template-columns:1fr 2fr 1fr 1fr 1fr;align-items:center;text-align:center;padding:10px}.orderDetailsItem p{font-size:12px}.arrowWrapper{position:relative;transition:all .3s ease 0s}.arrowWrapper:before{content:"";position:absolute;right:-20px;top:-10px;width:50px;height:0px;font-size:12px;padding-top:2px;border-radius:3px;transform:scaleY(0);transform-origin:bottom;transition:all .3s ease 0s}.arrowWrapper:hover:before{content:"Состав заказа";position:absolute;right:-20px;top:-10px;width:50px;height:30px;font-size:12px;padding-top:2px;border-radius:3px;background:#41b646;transform:scaleY(1)}.arrow{position:absolute;right:10px;top:20px;font-size:25px;transition:all .2s ease 0s;cursor:pointer}.arrow_active{transform:rotate(180deg)}